import React, { Component } from 'react';
import axios from 'axios';

let def = {};

class Form extends Component {
    state = {

    }
    onChange = (e) => {
        this.setState({[e.target.name]: e.target.value});
    }
    submit = () => {
        let data = {};
        for (let ele in this.state) {
            if (this.state[ele] === '') {
                data[ele] = def[ele];
            } else {
                data[ele] = parseInt(this.state[ele]);
            }
        }
        axios.post('api/model', {
            data: data
        })
            .then(req => this.props.fetmodels());
    }
    componentWillMount() {
        this.props.model.forEach(ele => {
            this.setState({[ele.name]: ''});
            def[ele.name] = ele.val;
        });
    }
    render() {
        return(
            <div className="col-md-5 col-sm-10 col-xs-12">
                <h1 className="slim">Create new simulation</h1>
                <form id="myForm">
                {
                    this.props.model.map(item => (
                    <div className="input-group">
                        <span className="input-group-addon" style={{minWidth: '150px'}}>{item.label}</span>
                        <input type="text" className="form-control" onChange={(e) => {this.onChange(e)}} name={item.name} placeholder={item.plc}/>
                    </div>
                    ))
                }
                <br/><br/>
                {
                    (this.props.login)?
                    <a className="btn btn-primary btn-block" onClick={() => {this.submit()}}>Create</a>
                    :
                    <a className="btn btn-primary btn-block" disabled>Create</a>
                }
                </form>
                <br/><br/>
            </div>
        );
    }
}

export default Form;
