import React, { Component } from 'react';
import Header from '../containers/Header';
import Footer from '../containers/Footer';
import Form from '../components/Form';
import Models from '../components/Models';
import axios from 'axios';

class modelHTML {
    constructor(label, name, val, unit) {
        this.label = label;
        this.name  = name;
        this.val   = val;
        this.unit  = unit;
        this.plc   = `${val} ${unit}`;
    }
}

class Main extends Component {
    state = {
        model: [
            new modelHTML('Gravity constant'            , 'g' , 9.8 , '(m/s^2)' ),
            new modelHTML('Viscosity constant'          , 'n' , 8E-2, '(kg/m s)'),
            new modelHTML('Density'                     , 'd' , 1E3 , '(kg/m^3)'),
            new modelHTML('Width of sink'               , 'x' , 0.3 , '(m)'     ),
            new modelHTML('Length of sink'              , 'z' , 0.1 , '(m)'     ),
            new modelHTML('Height of sink'              , 'y' , 0.1 , '(m)'     ),
            new modelHTML('Number of divisions (x axis)', 'Nx', 100 , ''        ),
            new modelHTML('Number of divisions (z axis)', 'Nz', 25  , ''        ),
            new modelHTML('Number of divisions (y axis)', 'Ny', 25  , ''        )
        ],
        models: []
    }
    fetmodels = () => {
        axios.get('api/model')
            .then(res => {
                console.log('axios res');
                this.setState({models: res.data});
                localStorage.setItem('models2D', JSON.stringify(res.data));
            });

    }
    componentDidMount() {
        this.fetmodels();
    }
    render() {
        console.log('state', this.state);
        return (
            <div className="container-fluid">
                <Header/>
                <div className="space-around">
                    <Form login={this.props.login} model={this.state.model} fetmodels={this.fetmodels}/>
                    <Models login={this.props.login} model={this.state.model} fetmodels={this.fetmodels} models={this.state.models}/>
                </div>
                <Footer/>
            </div>
        );
    }
}

export default Main;
